#calculator {
	width: 350px;
	height: 370px;
	margin-left: auto;
	margin-right: auto;
	padding: 5px;
	border: 1px solid #ebebeb;
}

#container {
	width: 700px;
	height: 600px;
	margin-left: auto;
	margin-right: auto;
	margin-top: 100px;
	text-align: center;
}
#operationsPad {
	float: right;
	margin-top: 20px;
}

#numberPad {
	float: left;
	margin-top: 20px;
}

#submit-button button {
	width: 75px;
	height: 55px;
	margin: 2px;
	float: right;
	clear: right;
	background-color: #4285f4;
	color: white;
	border: 1px solid #4285f4;
	font-size: large;
}
input {
	width: 345px;
	height: 50px;
	margin-right: auto;
	text-align: right;
	font-size: large;
	border: 1px solid #ebebeb;
}
#numberPad button {
	width: 75px;
	height: 65px;
	margin: 2px;
	background-color: #f1f3f4;
	border: 1px solid #f1f3f4;
	font-size: large;
}
#operationsPad button {
	width: 75px;
	height: 55px;
	margin: 2px;
	background-color: #dfe1e5;
	border: 1px solid #dfe1e5;
	font-size: large;
}
